import { useContext, useEffect, useState } from "react";
import { Link, Redirect, useParams } from "react-router-dom";
import Loader from "../../components/Loader";
import { AppContext } from "../../context/AppContext";
import { IApi } from "../../dtos/IApi";
import { IOwner } from "../../dtos/IOwner";
import { BaseService } from "../../services/base-service";
import { EPageStatus } from "../../types/EPageStatus";
import { IPageStatus } from "../../types/IPageStatus";
import { IRouteId } from "../../types/IRouteId";

export interface IApiCreateValues {
    name: string;
    baseUrl: string;
    description?: string;
    ownerId: string;
}

const initialFormValues: IApiCreateValues = {
    name: "",
    baseUrl: "",
    description: "",
    ownerId: "",
}

export interface IApiCreateProps {
    values: IApiCreateValues;
    handleChange: (target: HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement) => void;
    putApiData: (target: EventTarget) => void;
    pageStatus: IPageStatus;
    owners: IOwner[];
}

const ApisEdit = () => {
    let { id } = useParams() as IRouteId;

    const [pageStatus, setPageStatus] = useState({ pageStatus: EPageStatus.Loading, statusCode: -1 });
    const [formValues, setFormValues] = useState(initialFormValues);
    const [owners, setOwners] = useState([] as IOwner[]);

    const appState = useContext(AppContext);

    const loadData = async () => {
        const apiResult = await BaseService.get<IApi>('/v1/ApiBaseInformation', id, appState.jwt ?? "");
        if (apiResult.ok && apiResult.data) {
            setPageStatus({ pageStatus: EPageStatus.Ok, statusCode: 0 })
            setFormValues(apiResult.data)

            const ownerResult = await BaseService.getAll<IOwner>('/v1/Owners');

            if (ownerResult.ok && ownerResult.data) {
                setOwners(ownerResult.data);
            }
        } else {
            setPageStatus({ pageStatus: EPageStatus.Error, statusCode: apiResult.statusCode })
        }
    }

    useEffect(() => {
        loadData();
    }, []);

    const putApiData = async (e: EventTarget) => {
        setPageStatus({ pageStatus: EPageStatus.Loading, statusCode: -1 })

        let result = await BaseService.put<IApiCreateValues>("/v1/ApiBaseInformation", formValues, id, appState.jwt ?? "");

        if (result.ok) {
            setPageStatus({ pageStatus: EPageStatus.Ok, statusCode: result.statusCode })
        } else {
            setPageStatus({ pageStatus: EPageStatus.Error, statusCode: result.statusCode })
        }
    }

    const handleChange = (target: HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement) => {
        console.log(target.value);

        switch (target.id) {
            case "name":
                setFormValues({ ...formValues, name: target.value })
                return;
            case "baseUrl":
                setFormValues({ ...formValues, baseUrl: target.value })
                return;
            case "description":
                setFormValues({ ...formValues, description: target.value })
                return;
            case "ownerId":
                setFormValues({ ...formValues, ownerId: target.value })
                return;
            default:
                return;
        }

    }


    return (
        <>
            {appState.jwt !== null && pageStatus.statusCode !== 204
                ?
                <ApiEditFormView
                    values={formValues}
                    handleChange={handleChange}
                    putApiData={putApiData}
                    pageStatus={pageStatus}
                    owners={owners} />
                :
                <Redirect to="/apis" />
            }
        </>
    )
}

const ApiEditFormView = (props: IApiCreateProps) => {
    return (
        <>
            <div className="flex flex-wrap sm:flex-nowrap">
                <div className="flex-row pt-2 px-4 w-full sm:w-auto border-black bg-gradient-to-b from-pink-200 to-white dark:from-blue-900 dark:to-gray-800">
                    <Link
                        className="block p-2 bg-green-600 whitespace-nowrap w-full sm:w-auto text-center"
                        to="/apis/create">
                        Add new Base
                    </Link>
                </div>
                <div className="flex-row w-full">
                    <div className="flex flex-wrap justify-center p-2">
                        <form className="p-2 w-96 sm:w-120 border-2 dark:border-white border-black dark:text-white">
                            <div className="grid grid-col mb-2">
                                <label htmlFor="name">Name</label>
                                <input id="name"
                                    type="text"
                                    className="p-1 dark:text-black bg-gray-400"
                                    value={props.values.name}
                                    onChange={(e) => props.handleChange(e.target)}
                                >
                                </input>
                            </div>
                            <div className="grid grid-col mb-2">
                                <label htmlFor="baseUrl">Base URL</label>
                                <input id="baseUrl"
                                    type="text"
                                    className="p-1 dark:text-black bg-gray-400"
                                    value={props.values.baseUrl}
                                    onChange={(e) => props.handleChange(e.target)}
                                >
                                </input>
                            </div>
                            <div className="grid grid-col mb-2">
                                <label htmlFor="ownerId">Owner</label>
                                <select id="ownerId"
                                    className="p-1 dark:text-black bg-gray-400"
                                    value={props.values.ownerId}
                                    onChange={(e) => props.handleChange(e.target)}
                                >
                                    <option value=""></option>
                                    {
                                        props.owners.map(owner => {
                                            return (
                                                <option
                                                    value={owner.id}
                                                    key={owner.id}>
                                                    {owner.name}
                                                </option>
                                            )
                                        })
                                    }
                                </select>
                            </div>
                            <div className="grid grid-col mb-2">
                                <label htmlFor="description">Description</label>
                                <textarea id="description"
                                    className="p-1 dark:text-black bg-gray-400"
                                    value={props.values.description}
                                    onChange={(e) => props.handleChange(e.target)}
                                >
                                </textarea>
                            </div>
                            <div className="flex justify-center">
                                <button onClick={(e) => { props.putApiData(e.target); e.preventDefault() }} className="p-2 bg-green-800">Add API Base</button>
                            </div>
                        </form>
                    </div>
                    <Loader pageStatus={props.pageStatus} />
                </div>
            </div>
        </>
    )
}

export default ApisEdit;
