import React, { useContext, useState } from "react";
import { Redirect } from "react-router-dom";
import Loader from "../../components/Loader";
import { AppContext } from "../../context/AppContext";
import { BaseService } from "../../services/base-service";
import { EPageStatus } from "../../types/EPageStatus";
import { IPageStatus } from "../../types/IPageStatus";

export interface ICreateOwnerFormValues {
    name: string;
    description?: string;
}

const initialFormValues: ICreateOwnerFormValues = {
    name: "",
    description: "",
}

export interface IOwnersCreateFormProps {
    values: ICreateOwnerFormValues;
    handleChange: (target: HTMLInputElement) => void;
    postOwnerForm: (target: EventTarget) => void;
    pageStatus: IPageStatus;
}

const OwnersCreate = () => {
    const [formValues, setFormValues] = useState(initialFormValues);
    const [pageStatus, setPageStatus] = useState({ pageStatus: EPageStatus.Ok, statusCode: 0 });
    const appState = useContext(AppContext);

    const postOwnerForm = async (e: EventTarget) => {
        setPageStatus({ pageStatus: EPageStatus.Loading, statusCode: -1 });

        if (!appState.jwt) {
            setPageStatus({ pageStatus: EPageStatus.Error, statusCode: -1 });
            return;
        };

        const result = await BaseService.post<ICreateOwnerFormValues>("/v1/Owners", formValues, appState.jwt);

        if (result.ok) {
            setPageStatus({ pageStatus: EPageStatus.Ok, statusCode: result.statusCode });
        } else {
            setPageStatus({ pageStatus: EPageStatus.Error, statusCode: result.statusCode });
            console.log(pageStatus);
        }
    }

    const handleChange = (target: HTMLInputElement) => {
        switch (target.id) {
            case "ownerName":
                setFormValues({ ...formValues, name: target.value })
                return;
            case "ownerDesc":
                setFormValues({ ...formValues, description: target.value })
                return;
            default:
                return;
        }
    }

    return (
        <>
            {appState.jwt !== null && pageStatus.statusCode !== 201
                ?
                <OwnerCreateFormVIew
                    values={formValues}
                    handleChange={handleChange}
                    postOwnerForm={postOwnerForm}
                    pageStatus={pageStatus} />
                :
                <Redirect to="/owners" />
            }
        </>
    )
}

const OwnerCreateFormVIew = (props: IOwnersCreateFormProps) => {
    return (
        <>
            <div className="flex justify-center p-2">
                <form className="p-2 w-96 sm:w-120 border-2 dark:border-white border-black dark:text-white">
                    <div className="grid grid-col mb-2">
                        <label htmlFor="ownerName">Owner name</label>
                        <input id="ownerName"
                            type="text"
                            className="p-1 dark:text-black bg-gray-400"
                            value={props.values.name}
                            onChange={(e) => props.handleChange(e.target)}
                        >
                        </input>
                    </div>
                    <div className="grid grid-col mb-2">
                        <label htmlFor="ownerDesc">Owner description</label>
                        <input id="ownerDesc"
                            type="text"
                            className="p-1 dark:text-black bg-gray-400"
                            value={props.values.description}
                            onChange={(e) => props.handleChange(e.target)}
                        >
                        </input>
                    </div>
                    <div className="flex justify-center">
                        <button onClick={(e) => { props.postOwnerForm(e.target); e.preventDefault() }} className="p-2 bg-green-800">
                            Add Owner
                        </button>
                    </div>
                </form>
            </div>
            <Loader pageStatus={props.pageStatus} />
        </>
    )
}

export default OwnersCreate;
