import React, { useContext, useEffect, useState } from "react";
import { Redirect, useParams } from "react-router-dom";
import Loader from "../../components/Loader";
import { AppContext } from "../../context/AppContext";
import { BaseService } from "../../services/base-service";
import { EPageStatus } from "../../types/EPageStatus";
import { IPageStatus } from "../../types/IPageStatus";
import { IRouteId } from "../../types/IRouteId";

export interface ICreateEndpointValues {
    endpointUrl: string;
    apiBaseInformationId: string;
}

const initialFormValues: ICreateEndpointValues = {
    endpointUrl: "",
    apiBaseInformationId: ""
}

export interface ICreateEndpointProps {
    values: ICreateEndpointValues;
    handleChange: (target: HTMLInputElement) => void;
    postEndpointUrl: (target: EventTarget) => void;
    pageStatus: IPageStatus;
}

const CreateEndpoint = () => {
    const [formValues, setFormValues] = useState(initialFormValues);
    const [pageStatus, setPageStatus] = useState({ pageStatus: EPageStatus.Ok, statusCode: 0 });
    const appState = useContext(AppContext);

    let { id } = useParams() as IRouteId;

    useEffect(() => {
        setFormValues({ ...formValues, apiBaseInformationId: id });
    }, [])

    const postEndpointUrl = async (e: EventTarget) => {
        setPageStatus({ pageStatus: EPageStatus.Loading, statusCode: -1 });

        if (!appState.jwt) {
            setPageStatus({ pageStatus: EPageStatus.Error, statusCode: -1 });
            return;
        };

        let result = await BaseService.post<ICreateEndpointValues>("/v1/ApiEndpoints", formValues, appState.jwt);

        if (result.ok && result.data) {
            setPageStatus({ pageStatus: EPageStatus.Ok, statusCode: result.statusCode });
        } else {
            setPageStatus({ pageStatus: EPageStatus.Error, statusCode: result.statusCode });
        }
    }

    const handleChange = (target: HTMLInputElement) => {
        switch (target.id) {
            case "endpointUrl":
                setFormValues({ ...formValues, endpointUrl: target.value })
                return;
            default:
                return;
        }

    }
    return (
        <>
            {appState.jwt !== null && pageStatus.statusCode !== 201
                ?
                <CreateEndpointFormView
                    values={formValues}
                    handleChange={handleChange}
                    postEndpointUrl={postEndpointUrl}
                    pageStatus={pageStatus} />
                :
                <Redirect to="/apis" />
            }
        </>
    )
}

const CreateEndpointFormView = (props: ICreateEndpointProps) => {
    return (
        <>
            <div className="flex justify-center p-2">
                <form className="p-2 w-96 sm:w-120 border-2 dark:border-white border-black dark:text-white">
                    <div className="grid grid-col mb-2">
                        <label htmlFor="endpointUrl">Endpoint URL</label>
                        <input id="endpointUrl"
                            type="text"
                            className="p-1 dark:text-black bg-gray-400"
                            value={props.values.endpointUrl}
                            onChange={(e) => props.handleChange(e.target)}
                        >
                        </input>
                    </div>
                    <div className="flex justify-center">
                        <button onClick={(e) => { props.postEndpointUrl(e.target); e.preventDefault() }} className="p-2 bg-green-800">Add API Base</button>
                    </div>
                </form>
            </div>
            <Loader pageStatus={props.pageStatus} />
        </>
    )
}

export default CreateEndpoint;
