import React, { useState } from 'react';
import { Route, Switch } from 'react-router-dom';
import Header from './components/Header';
import ApisCreate from './containers/apis/ApisCreate';
import ApisEdit from './containers/apis/ApisEdit';
import ApisIndex from './containers/apis/ApisIndex';
import CreateEndpoint from './containers/endpoints/CreateEndpoint';
import HomeIndex from './containers/home/HomeIndex';
import LoginForm from './containers/identity/Login';
import RegisterForm from './containers/identity/Register';
import OwnersCreate from './containers/owners/OwnersCreate';
import OwnersEdit from './containers/owners/OwnersEdit';
import OwnersIndex from './containers/owners/OwnersIndex';
import Page404 from './containers/Page404';
import { AppContextProvider, initialAppState } from './context/AppContext';

function App() {
    const setAuthInfo = (jwt: string | null, fullName: string): void => {
        setAppState({ ...appState, jwt, fullName });
    }

    const [appState, setAppState] = useState({ ...initialAppState, setAuthInfo })

    return (
        <AppContextProvider value={appState}>
            <div className="dark:bg-gray-800 min-h-screen">
                <Header />
                <main>
                    <Switch>
                        <Route exact path="/" component={HomeIndex} />

                        <Route exact path="/identity/login" component={LoginForm} />
                        <Route exact path="/identity/register" component={RegisterForm} />

                        <Route exact path="/apis/edit/:id" component={ApisEdit} />
                        <Route exact path="/apis/create" component={ApisCreate} />
                        <Route exact path="/apis" component={ApisIndex} />

                        <Route exact path="/owners/edit/:id" component={OwnersEdit} />
                        <Route exact path="/owners/create" component={OwnersCreate} />f
                        <Route exact path="/owners" component={OwnersIndex} />

                        <Route exact path="/apis/:id/endpoint/create" component={CreateEndpoint} />

                        <Route component={Page404} />
                    </Switch>
                </main>
            </div>
        </AppContextProvider>
    );
}

export default App;
