import React, { useContext, useState } from "react";
import { Link, NavLink } from "react-router-dom";
import { AppContext } from "../context/AppContext";

const Header = () => {

    //#region Theme
    const changeTheme = (theme: 'dark' | '') => {
        let html = document.querySelector('html');
        if (theme === 'dark' && !html?.classList.contains('dark')) {
            updateButtonValue('â˜€');
            html?.classList.add(theme);
        } else {
            html?.classList.remove('dark');
            updateButtonValue('ðŸŒ‘')
        }
    }

    const isDarkTheme = (): boolean => {
        let html = document.querySelector('html');
        if (html?.classList.contains('dark')) {
            return true;
        } else {
            return false;
        }
    }
    const [buttonValue, updateButtonValue] = useState(isDarkTheme() ? 'â˜€' : 'ðŸŒ‘');

    //#endregion

    //#region AppState

    let appState = useContext(AppContext)

    const logOut = (): void => {
        appState.setAuthInfo(null, '');
    }

    //#endregion

    return (
        <header className="flex bg-pink-200 dark:bg-blue-900 p-2 justify-between">
            <div className="flex">
                <ul className="flex flex-wrap flex-row list-none">
                    <li className="flex-auto">
                        <Link className="block px-2 dark:text-white " to="/" title="Home">
                            <span className="text-2xl animal"></span>
                        </Link>
                    </li>
                    <li className="flex-auto">
                        <NavLink className="block p-2 dark:text-white dark:hover:bg-pink-100 dark:hover:text-black" to="/apis">
                            Apis
                        </NavLink>
                    </li>
                    <li className="flex-auto">
                        <NavLink className="block p-2 dark:text-white dark:hover:bg-pink-100 dark:hover:text-black" to="/owners">
                            Owners
                        </NavLink>
                    </li>
                </ul>
            </div>
            <div className="flex">
                <ul className="flex flex-wrap flex-row list-none">
                    {appState.jwt === null
                        ?
                        <>
                            <li className="flex-auto">
                                <NavLink className="block p-2 dark:text-white dark:hover:bg-pink-100 dark:hover:text-black" to="/identity/login">
                                    Login
                                </NavLink>
                            </li>
                            <li className="flex-auto">
                                <NavLink className="block p-2 dark:text-white dark:hover:bg-pink-100 dark:hover:text-black" to="/identity/register">
                                    Register
                                </NavLink>
                            </li>
                        </>
                        :
                        <li>
                            <button
                                onClick={() => logOut()}
                                className="block p-2 dark:text-white dark:hover:bg-pink-100 dark:hover:text-black">
                                Log Out
                            </button>
                        </li>
                    }
                </ul>
                {
                    isDarkTheme()
                        ?
                        <button className="text-white" onClick={() => changeTheme('')}>
                            {buttonValue}
                        </button>
                        :
                        <button onClick={() => changeTheme('dark')}>
                            {buttonValue}
                        </button>
                }
            </div>
        </header>
    )
}


export default Header;
