import React, { useContext, useEffect, useState } from "react";
import { Link } from "react-router-dom";
import Loader from "../../components/Loader";
import { AppContext } from "../../context/AppContext";
import { IOwner } from "../../dtos/IOwner";
import { BaseService } from "../../services/base-service";
import { EPageStatus } from "../../types/EPageStatus";
import { IPageStatus } from "../../types/IPageStatus";

const OwnersIndex = () => {
    const [owners, setOwners] = useState([] as IOwner[])
    const [pageStatus, setPageStatus] = useState({ pageStatus: EPageStatus.Loading, statusCode: -1 } as IPageStatus)

    const appContext = useContext(AppContext);

    const initialSelectedOwner: IOwner | null = {
        id: "",
        name: "",
        description: "",
        apis: []
    }

    const [selectedOwner, setSelectedOwner] = useState(initialSelectedOwner);

    const loadData = async () => {
        let result = await BaseService.getAll<IOwner>('/v1/Owners');
        if (result.ok && result.data) {
            setPageStatus({ pageStatus: EPageStatus.Ok, statusCode: 0 });
            setOwners(result.data);
        } else {
            setPageStatus({ pageStatus: EPageStatus.Error, statusCode: result.statusCode });
            console.log(pageStatus);
        }
    }

    const removeOwner = async (ownerId: string) => {
        setPageStatus({ pageStatus: EPageStatus.Loading, statusCode: -1 });

        let result = await BaseService.delete<IOwner>('/v1/Owners', ownerId, appContext.jwt ?? "");

        if (result.ok) {
            owners.splice(
                owners.findIndex((o) => o.id === ownerId),
                1
            );
            setSelectedOwner(initialSelectedOwner);
        }

        setPageStatus({ pageStatus: EPageStatus.Ok, statusCode: result.statusCode });
    }

    useEffect(() => {
        loadData();
    }, []);

    const CardDisplay = (props: { owner: IOwner }) => {
        return (
            <>
                <div
                    className="group block w-96 sm:w-120 p-2 border-black dark:border-white dark:text-white border-2 m-2 text-center dark:hover:bg-black hover:bg-gray-100 rounded-md"
                    key={props.owner.id}
                    onClick={(e) => appContext.jwt !== null ? selectedOwner.id !== props.owner.id ? setSelectedOwner(props.owner) : setSelectedOwner(initialSelectedOwner) : {}}>
                    {appContext.jwt !== null
                        ?
                        <>
                            <Link
                                className="float-left"
                                to={`/owners/edit/${props.owner.id}`}>
                                ðŸ“
                            </Link>
                            {
                                props.owner.apis === undefined || props.owner.apis.length < 1
                                    ?
                                    <button className="float-right"
                                        onClick={() => removeOwner(props.owner.id)}>
                                        âœ–
                                    </button>
                                    :
                                    <></>
                            }
                        </>
                        :
                        <></>
                    }
                    <div>
                        <div className="text-lg font-semibold group-hover:underline">{props.owner.name}</div>
                        <div className="flex flex-nowrap justify-between text-xs">
                            <div>
                            </div>
                            <div>
                            </div>
                        </div>
                    </div>
                    <hr />
                    <div className="italic py-2">
                        {props.owner.description}
                    </div>
                    <hr />
                    <div className="text-left py-2 space-y-2">
                        {
                            props.owner.apis?.map(api => (
                                <div key={api.id}>
                                    <div className="inline-flex w-full ">
                                        <input className="w-full p-2 group-hover:bg-gray-300 dark:group-hover:bg-gray-700 dark:bg-gray-500 bg-gray-200" disabled defaultValue={api.baseUrl} />
                                    </div>
                                </div>
                            ))
                        }
                    </div>
                </div>
            </>
        )
    }

    return (
        <>
            <div className="flex flex-wrap sm:flex-nowrap">
                <div className="sticky top-0 opacity-90 w-full sm:w-auto">
                    {
                        appContext.jwt !== null
                            ?
                            <div className="flex-row pt-2 px-4 w-full sm:w-44 border-black bg-gradient-to-b from-pink-200 to-white dark:from-blue-900 dark:to-gray-800 space-y-2 sticky top-0">
                                <Link
                                    className="block p-2 bg-green-600 whitespace-nowrap w-full text-center"
                                    to="/owners/create">
                                    Add new Owner
                                </Link>
                            </div>
                            :
                            <></>
                    }
                </div>
                <div className="flex-row">
                    <div className="flex flex-wrap justify-center">
                        {
                            owners.map(owner =>
                                <CardDisplay owner={owner} key={owner.id} />
                            )
                        }
                    </div>
                    <Loader pageStatus={pageStatus} />
                </div>
            </div>
        </>
    )
}

export default OwnersIndex;
