import { useContext, useEffect, useState } from "react";
import { Link } from "react-router-dom";
import Loader from "../../components/Loader";
import { AppContext } from "../../context/AppContext";
import { IApi } from "../../dtos/IApi";
import { IApiEndpoint } from "../../dtos/IApiEndpoint";
import { BaseService } from "../../services/base-service";
import { EPageStatus } from "../../types/EPageStatus";


const ApisIndex = () => {
    const [apis, setApis] = useState([] as IApi[])
    const [pageStatus, setPageStatus] = useState({ pageStatus: EPageStatus.Loading, statusCode: -1 });

    const appContext = useContext(AppContext)

    const initialSelectedApi: IApi | null = {
        id: "",
        ownerId: "",
        baseUrl: "",
        name: ""
    }

    const [selectedApi, setSelectedApi] = useState(initialSelectedApi);

    const loadData = async () => {
        let result = await BaseService.getAll<IApi>('/v1/ApiBaseInformation');
        if (result.ok && result.data) {
            setPageStatus({ pageStatus: EPageStatus.Ok, statusCode: 0 })
            setApis(result.data)
        } else {
            setPageStatus({ pageStatus: EPageStatus.Error, statusCode: result.statusCode })
        }
    }

    const removeApi = async (apiId: string) => {
        setPageStatus({ pageStatus: EPageStatus.Loading, statusCode: -1 });

        let result = await BaseService.delete<IApi>('/v1/ApiBaseInformation', apiId, appContext.jwt ?? "");
        if (result.ok) {
            apis.splice(
                apis.findIndex((a) => a.id === apiId),
                1
            );
            setSelectedApi(initialSelectedApi);
        }

        setPageStatus({ pageStatus: EPageStatus.Ok, statusCode: result.statusCode });
    }

    const removeEndpoint = async (endpointId: string, apiId: string) => {
        setPageStatus({ pageStatus: EPageStatus.Loading, statusCode: -1 });

        let result = await BaseService.delete<IApiEndpoint>('/v1/ApiEndpoints', endpointId, appContext.jwt ?? "");
        if (result.ok) {
            apis.forEach(api => {
                if (api.id === apiId) {
                    api.apiEndpoints?.forEach(endpoint => {
                        if (endpoint.id === endpointId) {
                            api.apiEndpoints?.splice(
                                api.apiEndpoints.findIndex(e => e.id === endpointId),
                                1
                            );
                        }
                    });
                }
            });
            setSelectedApi(initialSelectedApi);
        }

        setPageStatus({ pageStatus: EPageStatus.Ok, statusCode: result.statusCode });
    }

    useEffect(() => {
        loadData();
    }, []);

    const RowDisplay = (props: { api: IApi }) => {
        return (
            <>
                <div
                    className="group block w-96 sm:w-120 p-2 border-black dark:border-white dark:text-white border-2 m-2 text-center dark:hover:bg-black hover:bg-gray-100 rounded-md"
                    key={props.api.id}
                    onClick={(e) => appContext.jwt !== null ? selectedApi.id !== props.api.id ? setSelectedApi(props.api) : setSelectedApi(initialSelectedApi) : {}}>
                    {appContext.jwt !== null
                        ?
                        <>
                            <Link
                                className="float-left"
                                to={`/apis/edit/${props.api.id}`}>
                                ðŸ“
                            </Link>
                            {
                                props.api.apiEndpoints === undefined || props.api.apiEndpoints.length < 1
                                    ?
                                    <button className="float-right"
                                        onClick={() => removeApi(props.api.id)}>
                                        âœ–
                                    </button>
                                    :
                                    <></>
                            }
                        </>
                        :
                        <></>
                    }
                    <div>
                        <div className="text-lg font-semibold group-hover:underline">{props.api.name}</div>
                        <div className="flex flex-nowrap justify-between text-xs">
                            <div>
                                <a className="hover:underline" href={props.api.baseUrl} target="_blank" rel="noreferrer noopener">{props.api.baseUrl}</a>
                            </div>
                            <div>
                                {props.api.owner}
                            </div>
                        </div>
                    </div>
                    <hr />
                    <div className="italic py-2">
                        {props.api.description}
                    </div>
                    <hr />
                    <div className="text-left py-2 space-y-2">
                        {
                            props.api.apiEndpoints?.map(endpoint => (
                                <div key={endpoint.id}>
                                    <div className="inline-flex w-full ">
                                        <input className="w-full p-2 group-hover:bg-gray-300 dark:group-hover:bg-gray-700 dark:bg-gray-500 bg-gray-200" disabled defaultValue={props.api.baseUrl + endpoint.endpointUrl} />
                                        {
                                            appContext.jwt !== null
                                                ?
                                                <button
                                                    onClick={() => removeEndpoint(endpoint.id, endpoint.apiBaseInformationId)}
                                                    className="w-auto p-2 group-hover:bg-red-300 dark:group-hover:bg-red-700 dark:bg-gray-400 bg-gray-100">
                                                    X
                                                </button>
                                                :
                                                <></>
                                        }
                                    </div>
                                </div>
                            ))
                        }
                    </div>
                </div>
            </>
        )
    }

    return (
        <>
            <div className="flex flex-wrap sm:flex-nowrap">
                <div className="sticky top-0 opacity-90 w-full sm:w-auto">
                    {
                        appContext.jwt !== null
                            ?
                            <div className="flex-row pt-2 px-4 w-full sm:w-44 border-black bg-gradient-to-b from-pink-200 to-white dark:from-blue-900 dark:to-gray-800 space-y-2 sticky top-0">
                                <Link
                                    className="block p-2 bg-green-600 whitespace-nowrap w-full text-center"
                                    to="/apis/create">
                                    Add new Base
                                </Link>
                                {
                                    selectedApi.id !== ""
                                        ?
                                        <Link
                                            className="block p-2 bg-green-600 w-full sm:w-auto text-center"
                                            to={`/apis/${selectedApi.id}/endpoint/create`}>
                                            Add Endpoint to {selectedApi.name}
                                        </Link>
                                        :
                                        <div className="dark:text-white text-center">
                                            Click on API Card to add new EndpointðŸ˜Ž
                                        </div>
                                }
                            </div>
                            :
                            <></>
                    }
                </div>
                <div className="flex-row">
                    <div className="flex flex-wrap justify-center">
                        {
                            apis.map(api =>
                                <RowDisplay api={api} key={api.id} />
                            )
                        }
                    </div>
                    <Loader pageStatus={pageStatus} />
                </div>
            </div>
        </>
    );
}

export default ApisIndex;
