import React, { useContext, useEffect, useState } from "react";
import { Redirect, useParams } from "react-router-dom";
import Loader from "../../components/Loader";
import { AppContext } from "../../context/AppContext";
import { IOwner } from "../../dtos/IOwner";
import { BaseService } from "../../services/base-service";
import { EPageStatus } from "../../types/EPageStatus";
import { IPageStatus } from "../../types/IPageStatus";
import { IRouteId } from "../../types/IRouteId";

export interface IEditOwnerFormValues {
    name: string;
    description?: string;
}

const initialFormValues: IEditOwnerFormValues = {
    name: "",
    description: "",
}

export interface IOwnersCreateFormProps {
    values: IEditOwnerFormValues;
    handleChange: (target: HTMLInputElement) => void;
    putOwnerData: (target: EventTarget) => void;
    pageStatus: IPageStatus;
}

const OwnersEdit = () => {
    let { id } = useParams() as IRouteId;
    const [pageStatus, setPageStatus] = useState({ pageStatus: EPageStatus.Loading, statusCode: -1 } as IPageStatus);
    const [formValues, setFormValues] = useState(initialFormValues);

    const appState = useContext(AppContext);

    const loadData = async () => {
        const ownerResult = await BaseService.get<IOwner>('/v1/Owners', id, appState.jwt ?? "");
        if (ownerResult.ok && ownerResult.data) {
            setPageStatus({ pageStatus: EPageStatus.Ok, statusCode: 0 })
            setFormValues(ownerResult.data)
        } else {
            setPageStatus({ pageStatus: EPageStatus.Error, statusCode: ownerResult.statusCode })
        }
    }


    useEffect(() => {
        loadData();
    }, []);

    const putOwnerData = async (e: EventTarget) => {
        setPageStatus({ pageStatus: EPageStatus.Loading, statusCode: -1 })

        let result = await BaseService.put<IEditOwnerFormValues>("/v1/Owners", formValues, id, appState.jwt ?? "");

        if (result.ok) {
            setPageStatus({ pageStatus: EPageStatus.Ok, statusCode: result.statusCode })
        } else {
            setPageStatus({ pageStatus: EPageStatus.Error, statusCode: result.statusCode })
        }
    }

    const handleChange = (target: HTMLInputElement) => {
        switch (target.id) {
            case "ownerName":
                setFormValues({ ...formValues, name: target.value })
                return;
            case "ownerDesc":
                setFormValues({ ...formValues, description: target.value })
                return;
            default:
                return;
        }
    }

    return (
        <>
            {appState.jwt !== null && pageStatus.statusCode !== 204
                ?
                <OwnerCreateFormVIew
                    values={formValues}
                    handleChange={handleChange}
                    putOwnerData={putOwnerData}
                    pageStatus={pageStatus} />
                :
                <Redirect to="/owners" />
            }
        </>
    )
}

const OwnerCreateFormVIew = (props: IOwnersCreateFormProps) => {
    return (
        <>
            <div className="flex justify-center p-2">
                <form className="p-2 w-96 sm:w-120 border-2 dark:border-white border-black dark:text-white">
                    <div className="grid grid-col mb-2">
                        <label htmlFor="ownerName">Owner name</label>
                        <input id="ownerName"
                            type="text"
                            className="p-1 dark:text-black bg-gray-400"
                            value={props.values.name}
                            onChange={(e) => props.handleChange(e.target)}
                        >
                        </input>
                    </div>
                    <div className="grid grid-col mb-2">
                        <label htmlFor="ownerDesc">Owner description</label>
                        <input id="ownerDesc"
                            type="text"
                            className="p-1 dark:text-black bg-gray-400"
                            value={props.values.description}
                            onChange={(e) => props.handleChange(e.target)}
                        >
                        </input>
                    </div>
                    <div className="flex justify-center">
                        <button onClick={(e) => { props.putOwnerData(e.target); e.preventDefault() }} className="p-2 bg-green-800">
                            Add Owner
                        </button>
                    </div>
                </form>
            </div>
            <Loader pageStatus={props.pageStatus} />
        </>
    )
}

export default OwnersEdit;
